import {
  DatetimeValue,
  ProjectId,
  ProjectStopFlag,
  ProjectWorkStatusId,
  StatusReason,
  WorkStatus,
} from '../value';

export class ProjectWorkStatus {
  readonly projectWorkStatusId: ProjectWorkStatusId;
  readonly isProjectStop: ProjectStopFlag;
  readonly projectId: ProjectId;
  readonly statusName: WorkStatus;
  readonly statusReason: StatusReason;
  readonly statusTimestamp: DatetimeValue;

  constructor(args: {
    projectWorkStatusId: ProjectWorkStatusId;
    isProjectStop: ProjectStopFlag;
    projectId: ProjectId;
    statusName: WorkStatus;
    statusReason: StatusReason;
    statusTimestamp: DatetimeValue;
  }) {
    this.projectWorkStatusId = args.projectWorkStatusId;
    this.isProjectStop = args.isProjectStop;
    this.projectId = args.projectId;
    this.statusName = args.statusName;
    this.statusReason = args.statusReason;
    this.statusTimestamp = args.statusTimestamp;
  }
}
